* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "Hanken Grotesk", Arial, sans-serif;
  background-color: #262626;
  color: #d9d9d9;
  padding: 20px;
  height: 100vh;
}
main {
  display: flex;
  flex-direction: column;
  align-items: center;
}
header {
  text-align: center;
}
header > h1 {
  font-weight: 500;
  font-size: 1.25rem;
  color: #f9f4da;
}
header > p {
  font-size: 0.87rem;
  max-width: 350px;

  color: #8e8e8e;
}
section.game-status {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  max-width: 350px;
  border-radius: 4px;
  margin-block: 30px;
  text-align: center;
 transition: 0.4s ease-in-out;
}
section.game-status.won {
  background-color: #10a95b;
 transform: scale(1.3);
}
section.game-status.lost {
  background-color: #ba2a2a;
 transform: scale(o.9);
}
section.game-status.farewell {
 background-color: #7A5EA7;
 border: 1px dashed #323232;
}
section.game-status.encourage {
 background-color: #069bb2;
 border: 1px dashed #323232;
 transform: scale(1.2);
}

section.game-status > h2 {
  font-size: 1.25rem;
  margin: 5px;
}
.game-status > p {
  margin: 5px;
}
.game-status > p.farewell-message{
  
  padding: 20px 0;
 font-style: italic;
 font-weight: 400;
}
section.level-chips {
  display: flex;
  flex-wrap: nowrap;
  gap: 5px;
  justify-content: center;
  max-width: 500px;
  margin-bottom: 10px;
  font-size: 10.7px;
  font-weight: bold;
}
span.chip {
  padding: 8px 4.5px;
  border-radius: 4px;
  position: relative;
}
/* Dimmed overey with skull on "lost" levels */
span.chip.lost::before {
  content: "ðŸ’€";
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
  font-size: 0.85rem;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.7);
}
section.word {
  display: flex;
  gap: 2px;
  justify-content: center;
  margin-bottom: 5px;
}
.word > span {
  background-color: #323232;
  height: 40px;
  width: 40px;
  display: flex;
  justify-content: center;
  font-size: 1.125rem;
  align-items: center;
  border-bottom: solid 1px #f9f4da;
}

section.word > span.missed-letter{
  color: #EC5D49;
}
section.keyboard {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  justify-content: center;
  max-width: 450px;
  margin-bottom: 36px;
  margin-top: 20px;
}
section.keyboard > button {
  height: 35px;
  width: 35px;
  background-color: #fcba29;
  border: 1px solid #d7d7d7;
  border-radius: 3px;
  cursor: pointer;
}
section.keyboard > button.correct {
  background-color: #10a95b;
}
section.keyboard > button.wrong {
  background-color: #ec5d49;
}
section.keyboard > button:disabled{
  cursor: not-allowed;
  opacity: 0.8;
}

button.new-game {
  background-color: #11b5e5;
  border: 1px solid #d7d7d7;
  border-radius: 4px;
  width: 225px;
  height: 40px;
  padding: 6px 12px;
  display: block;
  margin-inline: auto;
  cursor: pointer;
}
.sr-only{
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}
.progress-bar-container{
  height: 6px;
  max-width: 500px;
  min-width: 460px;
  width: 100%;
  background-color: #ddd;
  border-radius: 3px;
  overflow: hidden;
  margin-bottom: 26px;
  
}
.progress-bar{
  height: 100%;
  transition: width 0.9s ease, background-color 0.9s ease;

}

button.hint-btn{
  
  background-color: #11b5e5;
  border: 1px solid #d7d7d7;
  border-radius: 4px;
  padding: 2px 6px;
  display: block;
  margin-inline: auto;
  cursor: pointer;
  font-weight: bold;
}

button.hint-btn:disabled{
   cursor: not-allowed;
  opacity: 0.8;

}









.start-screen{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 70vh;
}
.start-btn {
  
  background-color: #11b5e5;
  border: 1px solid #d7d7d7;
  color: white;
  font-size: 1.2rem;
  border-radius: 4px;
  width: 225px;
  height: 40px;
  padding: 6px 12px;
  display: block;
  margin-inline: auto;
  cursor: pointer;
  transition: 0.4s ease-in-out;
}

.start-btn:hover {
  transform: scale(1.2);
}
.level-screen{
   display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 70vh;
}
.level-btn{
  display: flex;
  flex-direction: row;
  gap: 8px;
}
.level-btn>button{
  font-weight: bold;
  padding: 6px 12px;
  background-color: #11b5e5;
  border: 1px solid #d7d7d7;
  color: white;
  font-size: 1.2rem;
  border-radius: 4px;
  width: 150px;
  height: 40px;
  display: block;
  margin-inline: auto;
  cursor: pointer;
  transition: 0.4s ease-in-out;

}

.level-btn>button:hover{
  background-color: #0b6a87;
  border: 1px solid #11b5e5;

}
